import * as React from "react";
import { motion } from "framer-motion";

const Path = props => (
  <motion.path
    fill="transparent"
    strokeWidth="2.3"
    stroke="#fff"
    strokeLinecap="round"
    {...props}
  />
);

export default function MenuToggle({ toggle }){
    return(
    <button type="button" onClick={toggle} className="mx-[auto] outline-none buttonreset">
        <svg width="27" height="23" viewBox="0 0 23 23">
        <Path
            variants={{
            closed: { d: "M 1 2.5 L 20 2.5" },
            open: { d: "M 3 16.5 L 17 2.5" }
            }}
        />
        <Path
            d="M 1 9.423 L 20 9.423"
            variants={{
            closed: { opacity: 1 },
            open: { opacity: 0 }
            }}
            transition={{ duration: 0.1 }}
        />
        <Path
            variants={{
            closed: { d: "M 1 16.346 L 20 16.346" },
            open: { d: "M 3 2.5 L 17 16.346" }
            }}
        />
        </svg>
    </button>
)};
