import * as THREE from 'three';
import { Canvas, useThree, useFrame } from "@react-three/fiber";
import { OrbitControls, Preload, useGLTF, useAnimations } from "@react-three/drei";
import { Suspense , useRef, useEffect, useState } from "react";
import { EffectComposer, SSAO, Bloom } from "@react-three/postprocessing";
import { KernelSize, BlendFunction } from 'postprocessing'


export function Model(props) {
  const group = useRef()
  const { nodes, materials, animations } = useGLTF('macbook.gltf')
  const { actions, names } = useAnimations(animations, group)
 // useEffect(() =>{ animations.forEach((clip) => { actions[clip.name].play() })}, [animations, actions])
 useEffect(() =>{actions[names[0]].play(); actions[names[1]].play();  actions[names[0]].clampWhenFinished = true; actions[names[1]].clampWhenFinished = true; actions[names[0]].setLoop(1,1); actions[names[1]].setLoop(1,1);}, [actions, names])

  //useEffect(() =>{ animations.forEach((clip) => { actions[clip.name].play()})}, [animations, actions])
  return (
    <group ref={group} {...props} dispose={null}>
      <group name="Scene">
        <group name="Empty001" position={[0, -0.14, -0.09]} rotation={[-0.26, 0, 0]} scale={0.26}>
          <group name="Cube016" position={[0, -0.01, 0.01]} rotation={[1.65, 0, 0]} scale={1.06}>
            <mesh name="Cube011" geometry={nodes.Cube011.geometry} material={materials.body} />
            <mesh name="Cube011_1" geometry={nodes.Cube011_1.geometry} material={materials['black.002']} />
            <mesh name="Cube011_2" geometry={nodes.Cube011_2.geometry} material={nodes.Cube011_2.material} />
            <mesh name="Cube011_3" geometry={nodes.Cube011_3.geometry} material={materials.dark} />
            <mesh name="Cube011_4" geometry={nodes.Cube011_4.geometry} material={materials['glass.003']} />
            <mesh name="Cube011_5" geometry={nodes.Cube011_5.geometry} material={nodes.Cube011_5.material} />
            <mesh name="Cube011_6" geometry={nodes.Cube011_6.geometry} material={nodes.Cube011_6.material} />
            <mesh name="Cube011_7" geometry={nodes.Cube011_7.geometry} material={materials['glass.001']} />
          </group>
        </group>
        <group name="Cube012" position={[0.01, -0.14, 0.09]} scale={0.26}>
          <mesh name="Cube013" geometry={nodes.Cube013.geometry} material={materials.key} />
          <mesh name="Cube013_1" geometry={nodes.Cube013_1.geometry} material={materials.key} />
          <mesh name="Cube013_2" geometry={nodes.Cube013_2.geometry} material={materials['Material.026']} />
        </group>
        <mesh name="Cube015" geometry={nodes.Cube015.geometry} material={materials.body} position={[0, -0.14, 0.34]} scale={0.12} />
        <mesh name="Cube021" geometry={nodes.Cube021.geometry} material={materials.body} position={[0, -0.15, 0.18]} rotation={[-0.02, 0, 0]} />
        <mesh name="Plane" geometry={nodes.Plane.geometry} material={materials['Apple-logo']} position={[0.02, -0.14, -0.09]} rotation={[2.96, 0, 0]} scale={[2.29, 2.29, 0.87]} />
      </group>
    </group>
  )
}

useGLTF.preload('macbook.gltf')






export default function Macbook() {
  const [onDemand, setOnDemand] = useState(false)
  setTimeout(()=>{setOnDemand(true);}, 4000)
  return (
    <Canvas flat  camera= {{position: [0, 0.2, 1]}} frameloop= {onDemand?'onDemand' : 'always'} >
      <directionalLight intensity={4} position={[0, 90, 5]} />
      <directionalLight intensity={4} position={[10, -5, -5]} />
      <directionalLight intensity={4} position={[-20, -10, 5]} />
      <directionalLight intensity={4} position={[20, -10, 5]} />
      <Suspense fallback={null}>
        <ambientLight intensity={2} />
        <Model />
        <OrbitControls enableZoom={false} />
      </Suspense>
      <Preload all />
    </Canvas>
  );
}
